* {

    scroll-behavior: smooth;

}

::-webkit-scrollbar {
    width: 12px;
    height: 12px;

}

::-webkit-slider-thumb {
    background-color: #697565;

}

::-webkit-scrollbar-track {
    background: black;
    /* border-radius: 10px; */
}

::-webkit-scrollbar-thumb {
    background-color: black;
    border-radius: 10px;
    border: 2px solid transparent;
}



::-webkit-scrollbar-thumb:hover {
    background-color: #555;
    cursor: pointer;

}

