.MainComponents-Container {
    height: 7vh;
    background-color: transparent;
    backdrop-filter: blur(15px);
    width: 100%;
    position: fixed;
    z-index: 9999;
    padding: 4px 0px 4px 0px;
    box-shadow: 0px 6px 14px -2px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 6px 14px -2px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 6px 14px -2px rgba(0, 0, 0, 0.75);
}


.Logo-div {
    margin-top: -1vh;
    color: white;
    font-size: xx-large;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: flex-start;
}

.Logo-div a {
    margin: 0px 0px 0px 5px;
    font-family: "Lavishly Yours", cursive;
    font-weight: 200;
    font-size: x-large;
    font-style: normal;
    color: white;
}



.Navlink {
    border-radius: 10px;
    background-color: transparent;
    text-decoration: none;
    color: wheat;
    /* width: 20vw; */
    /* background-color: orange; */
    font-family: "Playwrite HR Lijeva", cursive;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    padding: 5px 10px;
}

.Navlink:hover {
    color: white;
}


ul li {
    list-style: none;
    text-decoration: none;
    position: relative;
    width: auto;
}

.Navlink::before {
    content: " ";
    width: 0;
    /* background: linear-gradient(0.25turn, #006400, #2E8B57, #4CAF50, #3B6B3A, #1B3B2F); */
    background:linear-gradient(to left top,white,rgba(255, 255, 255, 0.667),rgba(255, 255, 255, 0.626));
    height: 2px;
    position: absolute;
    top: 30px;
    left: 1px;
    border-radius: 8px;
    transition: 1s ease-in-out;
    display: flex;
    justify-content: center;
}

.Navlink:hover::before {
    width: 100%;
    /* background:url("/public/Components/Background/7172054_3572249.jpg"); */
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.Navlink.active {
    color: #f4f4f4;
    /* Active link color */
    font-weight: bold;
    text-shadow: 1px 1px 5px #555;
}


.Nav-div {
    display: flex;
    border: 1px solid transparent;
    /* top: 8px; */
    margin-top: -7px;

}

.ul-items1 {

    width: 40%;
    display: flex;



}

.ul-items2 {
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.ul-items1 li {
    cursor: pointer;
}

a {
    text-decoration: none;
}

.Nav-Button {
    color: wheat;
    background-image: radial-gradient(circle farthest-corner at 32.7% 82.7%, #697565 8.3%, #181f16 79.4%);
    border: 0.5px solid rgba(14, 25, 147, 0.29);
    width: 8vw;
    height: 4vh;
    border-radius: 8px;
    cursor: pointer;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: "Playwrite HR Lijeva", cursive;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    transition: all 0.3s ease;
    animation: btn-anime 10s infinite ease-in-out;
    position: relative;
    overflow: hidden;
    border-color: #ffffffb3;
}
.Nav-Button:hover{
    box-shadow: 0px 0px 2px 5px rgba(54, 51, 51, 0.993);
    color: white;
}

.Nav-Button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.5) 10%, transparent 70%);
    opacity: 0;
    pointer-events: none;
    animation: spark-animation 2s infinite;
}

.Nav-Button:hover::before {
    animation: spark-animation 1s infinite;
}

@keyframes spark-animation {
    0%, 100% {
        opacity: 0;
        transform: scale(0);
    }
    50% {
        opacity: 1;
        transform: scale(1.5);
    }
}

@keyframes btn-anime {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}


